
.chapter-tag-modal.page-modal .modal-container {
  border-radius: 0;
  background-color: white;
  max-height: unset;
  padding: 0 !important;
  overflow-y: visible;
}
.chapter-tag-modal.page-modal .modal-container .chapter-selector {
  margin-top: -1px;
  font-size: 12px;
  min-height: 200px;
  overflow-y: auto;
  border: none;
  background-color: #f9f9f9;
  -webkit-box-shadow: inset 0 0 -1px rgba(0, 0, 0, 0.5);
          box-shadow: inset 0 0 -1px rgba(0, 0, 0, 0.5);
}
.chapter-tag-modal.page-modal .modal-container .chapter-selector .ui.basic.button {
  border: none;
}
.chapter-tag-modal.page-modal .modal-container .chapter-selector .book-tree {
  max-height: 60vh;
}
.chapter-tag-modal.page-modal .modal-container .collected-nodes {
  padding: 1em .5em;
  min-height: 5em;
  border-top: solid 1px #ddd;
}



