@color-blue: #rgb(66, 165, 245);

.ql-image-upload-overlay {
  position: absolute;
  color: #444;
  text-shadow: #fff 1px 0 1px;
}

.ql-container.ql-snow {
  border-radius: 0;
}

.ql-blank-placeholder {
  display: inline-block;
  vertical-align: bottom;
  .blank-placeholder-label {
    display: inline-block;
    text-align: center;
    line-height: 1.2;
    margin: 0 .2em;
    width: 4rem;
    border-bottom: solid 2px #4a4a4a;
    background-color: #f9f9f9;
  }
}



